.top-bar {
  padding-block: 24px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  @include onTablet {
   padding-block: 32px;
  }

  &__icons {
    display: flex;
    gap: 24px;
  }

  &__logo-link {
    display: flex;
  }

  &__logo {
    height: 16px;
    transition: transform 0.3s;

    @include onTablet {
      height: 24px;
    }

    @include hover (transform, scale(1.05));
  }
}

